<template>
<div class="user-home">
  <header class="head">
    <div class="head-icon">
      <img src="/static/icon/Receipt.png" >
      <div class="header-txt">一点回收</div>
    </div>
    
  </header>
  <main class="content">
    <div class="passage">
      <div class="passage-container">
      <div class="passage-item">
        <img src="/static/icon/orderSearch.png" alt="" class="passage-item-icon">

        <div class="passage-item-txt">点滴记录</div>
      </div>
      <div class="passage-item">
        <img src="/static/icon/scan.png" alt="" class="passage-item-icon">

        <div class="passage-item-txt">扫码开箱</div>
      </div>
      <div class="passage-item">
        <img src="/static/icon/ranking.svg" alt="" class="passage-item-icon">

        <div class="passage-item-txt">点滴排行</div>
      </div>
      </div>
    </div>
   <div class="func">
     <div class="func-container">
     <div class="func-item" v-for="item in func" :style="{backgroundImage: 'url(' + item.bgUrl + ')'}">
       <img class="func-item-icon" v-bind:src="item.imgUrl" ></img>
       <div>{{item.title}}</div>
     </div>

     </div>
   </div>
  </main>
  <footer class="nav-bar">
    <div class="nav-item"></div>
    <div class="nav-item"></div>
    <div class="nav-item"></div>
  </footer>
</div>
</template>
<script>
export default {
  data(){
    return{
      func:[
        {title:"纸板日",imgUrl:"/static/icon/package.png",bgUrl:"/static/img/wave-1.png"},
        {title:"废品价格",imgUrl:"/static/icon/price.png",bgUrl:"/static/img/wave-2.png"},
        {title:"点滴公益",imgUrl:"/static/icon/heart.png",bgUrl:"/static/img/wave-3.png"},
        {title:"积分交易",imgUrl:"/static/icon/transaction.png",bgUrl:"/static/img/wave-4.png"}
      ]

    }
  }
}
</script>
<style>
.head{
    font-family: PingFang-Regular;
    width: 100%;
    height: 198px;
    background:linear-gradient(to right, #6ee0ff, #32aafa) ;
    background: url(/static/img/top-wave.jpg);
    background-size: 100%;
}
.head-icon{
  margin: 0 auto;
  padding-top: 30px;
  height: 64px;
  width: 64px;
  text-align: center;
  color: #FFF;
  font-size: 14px;
}
.header-txt{
  margin-top: 10px;
}
.content{
  font-family: PingFang-Regular;
  width: 100%;
  position: relative;
  top: -48px;
}
.passage{

  box-shadow:1px 1px 10px rgba(161, 161, 161, 0.349019607843137);
  margin: 0 auto;
  height: 91px;
  width: 340px;
  border-radius: 5px;
  background-color: #FFF;
}
.passage-container{
  margin: 0 39px;
  display: flex;
  justify-content: space-between;

} 
.passage-item{
  margin-top: 19px;
  position: relative;
  height: 64px;
  width: 47px;
}
.passage-item-icon{
  margin-top: 9px;
 margin-left: 6px;
  height: 35px;
  width: 34px;
}
.passage-item-txt{
  position: absolute;
  bottom: 0;
  font-size: 12px;
  white-space:nowrap; 
}
.func{
  margin-top: 4px;
  width: 100%;
}
.func-container{
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  width: 340px;
  flex-wrap: wrap;
}
.func-item{
  box-shadow:1px 1px 10px rgba(161, 161, 161, 0.349019607843137);
  width: 160px;
  height: 119px;
  border-radius: 4px;
  text-align: center;
  font-size: 14px;
  color: #999999;
  margin-top: 20px;
  background-size:86%;
}
.func-item-icon{
  margin: 0 62px;
  margin-top: 27px;
  width: 36px;
  height: 36px;
}
.nav-bar{
  display: flex;
  justify-content: space-around;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 49px;
  border-top: #E4E4E4 solid 1px;
}
.nav-item{
  width: 36px;
  height: 44px;
}
img{width: 100%;height: 100%;}
@font-face {
  font-family: PingFang-Regular;
  src: url(../../assets/font/PingFangRegular.ttf);
}
</style>



